.CaixaPosts {
	background-color: #282c34;
	display: flex;
	font-size: 1rem;
	margin: 0.5rem;
	border-radius: 1rem;
}

.CaixaPosts:hover {
	filter: brightness(80%);
	cursor: pointer;
}

.ImagemDoPost {
	max-width: 200px;
	margin: 0.5em;
	border-radius: 2rem;
}

.CaixaDeTextoPosts {
	padding: 1rem;
	flex-grow: 1;
}
