.Caixa {
	background-color: #1f1f1f;
	border: solid;
	border-width: 0.1rem;
	display: flex;
	border-color: #4AF626;
	margin-bottom: 1vh;
}

.Caixa:hover {
	border-color: #FFF625;
	border-width: 0.2rem;
	cursor: pointer;
}

.Legenda {
	font-size: 1rem;
	align-content: center;
	display: flex;
	justify-content: space-between;
	padding-left: 5px;
	padding-right: 5px;
}

.Nome {
	display: inline;
	text-align: center;
	font-weight: bold;
}

.Github {
	display: flex;
}

.GithubIcon {
	text-decoration: none;
	align-self: center;
	color: white;
}

.Espacador {
	min-width: 1vh;
}
