.Botao {
	background-color: black;
	color: white;	
	border-radius: 0.25rem;
	text-decoration: none;
	margin-right: 2vh;
	display: flex;
	cursor: pointer;
}
.Botao:hover {
	background-color: white;
	color: black;
}

.NomeBotao {
	display: flex;
	flex-direction: column;
	align-content: end;
	align-self: center;
	margin-left: 4px;
	margin-right: 4px;
}
