.Principal {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Conteudo {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	background-color: black;
	border-radius: 0.1rem;
	min-width: 85vw;
	padding: 1rem;
}
